.xyz {
  color: #bebebe;
}
body {
  margin: 0px;
  padding: 0px;
}
.indexPage {
  width: 100%;
  margin: 0px;
  padding: 0px;
  height: 100%;
  position: absolute;
}
.menu {
  float: left;
  width: 200px;
  height: 100%;
  background-color: #000c17;
}
.menu .el-menu {
  border: none;
}
.menu .menuHeader {
  color: white;
  text-align: left;
  padding: 20px;
  padding-left: 30px;
}
.menu .el-menu-item {
  background-color: #000c17;
  color: #bebebe;
}
.menu .el-menu-item:hover {
  color: #efefef;
}
.menu .el-menu-item.is-active {
  background-color: #1890ff;
  color: white;
}
.content {
  float: left;
  width: calc(100% - 200px);
  position: relative;
  box-sizing: border-box;
  height: 100%;
  overflow-y: auto;
}
.content .contentHeader {
  padding: 10px 20px;
  box-sizing: border-box;
  line-height: 40px;
  box-shadow: 0px 0px 10px 0px rgba(100, 100, 100, 0.5);
  position: absolute;
  top: 0px;
  width: 100%;
}
.content .contentBody {
  box-sizing: border-box;
  margin-top: 61px;
}
.content .tabContent {
  padding: 20px;
  box-sizing: content-box;
}
.header {
  position: absolute;
  top: 0px;
  width: 100%;
  box-shadow: 0px 0px 10px 0px rgba(100, 100, 100, 0.5);
  overflow: visible;
  z-index: 10;
}
.header::after {
  content: "";
  display: block;
  visibility: hidden;
  clear: both;
}
.loginBox {
  line-height: 40px;
}
.loginBox .loginItem {
  margin-right: 10px;
}
.headerNav .el-tabs__nav-scroll {
  background-color: #ff9933;
  padding-top: 10px;
  padding-left: 100px;
}
.headerNav .el-tabs__nav-scroll .el-tabs__nav {
  border: none;
}
.headerNav .el-tabs__nav-scroll .el-tabs__item {
  border: none;
  color: white;
}
.headerNav .el-tabs__nav-scroll .el-tabs__item.is-active {
  background-color: white;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  color: black;
  font-weight: bold;
}