.treePop {
  min-width: 193px;
}
.treeSpan {
  display: block;
  height: 28px;
  line-height: 28px;
  box-sizing: border-box;
  vertical-align: middle;
}
.inlineItem {
  vertical-align: middle;
}
.treeContainer {
  vertical-align: middle;
  display: block;
  height: 32px;
  line-height: 28px;
  box-sizing: border-box;
  border: 1px solid #eaeaea;
  border-radius: 5px;
  background-color: white;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  position: relative;
}
.treeContainer .treeControl {
  position: absolute;
  right: 0px;
  top: 0px;
  width: 30px;
  height: 32px;
  background-color: white;
  line-height: 32px;
}
.treeContainer .treeControl > i {
  top: 8px;
  right: 10px;
  position: absolute;
  height: 10px;
  font-size: 14px;
  color: #c9c9c9;
  border-radius: 50%;
}
.treeContainer .treeControl > i.el-icon-arrow-up {
  transform-origin: 50% 7px;
  transform: rotate(180deg);
  transition: transform linear 0.2s;
}
.treeContainer .treeControl > i.el-icon-arrow-up.iconShow {
  transform: rotate(0deg);
}
.treeContainer:before {
  content: ' ';
  height: 100%;
  display: inline-block;
  vertical-align: middle;
}
.treeContainer:hover {
  border-color: #0c99fd;
  outline: none;
}
.treeContainer:focus {
  box-shadow: 0px 0px 8px 0px #0c99fd;
}
.treeContainer .el-tag {
  vertical-align: middle;
  margin-right: 6px;
}
.treeContainer .el-tag:first-child {
  margin-left: 6px;
}