/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
.uploadBtn {
  border: 1px solid #1890ff;
  color: white;
  background-color: #1890ff;
  border-radius: 5px;
  cursor: pointer;
  margin: 5px 0px;
}
.uploadBtn:hover {
  background-color: #0076e4;
  color: white;
}
.drasterBoard {
  height: 100%;
  width: 100%;
}
.drasterBoard .drasterInput {
  display: none !important;
}
.upLoadCoverDragBox {
  background-color: #f9f9f9;
  border: 1px dashed #efefef;
  display: block;
  height: 100%;
  width: 100%;
  cursor: pointer;
  box-sizing: border-box;
}
.upLoadCoverDragBox:before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}
.upLoadCoverDragBox .dragContent {
  width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  user-select: none;
}
.upLoadCoverDragBox .dragContent .uploadIcon {
  color: #4d8fff;
}
.upLoadCoverDragBox:focus {
  box-shadow: 0px 0px 25px 2px #4d8fff;
  color: #4d8fff;
}
.upLoadCoverDragBox.upLoadDragOver {
  border: 1px dashed #4d8fff;
  background-color: #c2c2c2;
  border-width: 2px;
}
.upLoadCoverDragBox:hover {
  border: 1px dashed #4d8fff;
  color: #4d8fff;
}
.upLoadCoverDragBox:hover .uploadIcon {
  color: #4d8fff;
}
.upLoadCoverDragBox.upLoadFocus {
  box-shadow: 0px 0px 25px 2px #4d8fff;
  color: #4d8fff;
}
.uploadFileList {
  font-size: 12px;
}
.uploadFileList .fileItem {
  position: relative;
}
.uploadFileList .fileItem .deleteBtn {
  cursor: pointer;
  position: absolute;
  height: 16px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  right: 0px;
  display: inline-block;
  font-size: 14px;
}
.uploadFileList .fileItem .deleteBtn:hover {
  color: #2b8aeb;
}